    font-size: 50px;
} */

.container .sign{
    font-family: 'Great Vibes', sans-serif;
    font-size: 40px;
    color: #41b0bf !important;
}

.container .fa-heart{
    font-size: 20px;
    padding: 1%;
    color: #dd1515;
}
